@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&family=Ubuntu+Mono&display=swap);

#header {
    color: #FFFFFF;
    padding-top: 20vh;
    padding-bottom: 20vh;
    padding-left: 7vw;
    padding-right: 7vw;
    background-image: url(/static/media/ps1logo.43ee75f7.png);
    background-repeat: no-repeat;
    background-size: 100% auto;
    object-fit: cover;
}

#banner{
    width: 100%;
    height: 70vh;
    object-fit: cover;
}

#game-library {
    margin: 20px;
}

#game-library h1:hover {
    color: #9898EC;
    text-decoration: underline;
}

#game-library a:link {
    color: #FFFFFF;
}
  
#game-library a:visited {
    color: #FFFFFF;
}

#game-img {
    width: 100%;
}

.carousel-img{
    width: 100%;
    height: 90vh;
    object-fit: cover;
}

#library-card{
    background-color: transparent;
    border: none;
    margin: -20px;
  }

  @media (max-width: 1024px) {

    #header h1{
        font-size: 1.5rem;
    }

    #header h3{
        font-size: 1.5rem;
    }

    #header{
        padding-top: 10vh;
        padding-bottom: 10vh;
    }

    #banner{
        height: 40vh;
    }

    .carousel-img{
      height: 50vh;
    }

    #carousel-header{
        font-size: 1.5rem;
    }
  }
#games-container {
    padding-top: 2vh;
}

#games {
    padding-top: 30vh;
    padding-left: 10vw;
}
#characters-container {
    padding-top: 2vh;
}

#characters {
    padding-top: 30vh;
    padding-left: 10vw;
}

#character-card{
    color: #FFFFFF;
    background-color: transparent;
    border: none;
}
#admin-container{
    background-color: #B2B2B2;
    color: #333333;
    margin: 0;
}

#admin-wrapper{
    padding: 50px;
}

h2{
    margin-bottom: 20px;
}

#admin-header{
    padding: 20px;
    text-align: center;
}

#admin-banner{
    width: 100vw;
}

#admin-navbar{
    background-color: #444444;
}

#admin-game-card{
    color: #FFFFFF;
    background-color: transparent;
    border: none;
    color: #333333;
}

.update-section{
    margin-bottom: 30px;
    position: -webkit-sticky;
    position: sticky;
    top: 50px;
}

.update-input{
    margin-bottom: 20px;
    width: 95%;
}

#delete-btn{
    margin-top: 10px;
}
#main-container {
  background-color: #000000;
  color: white;
  overflow: hidden;
}

body{
  margin: 0;
}

h1 {
  font-family: 'Press Start 2P', cursive;
}

h2 {
  font-family: 'Press Start 2P', cursive;
}

h3 {
  font-family: 'Ubuntu Mono', monospace;
}

p{
  font-family: 'Ubuntu Mono', monospace;
}

button{
  width: 100%;
}

#cards{
  color: #FFFFFF;
  background-color: transparent;
  border: none;
}

#logo {
  height: 50px;
  margin: 0;
  padding: 0;
}
footer {
  background-color: #0A0A0A;
  color: #444444;
  padding: 7vh;
  font-family: 'Ubuntu Mono', monospace;

  position: relative;
  bottom: 0;
}

#footer-header {
  font-size: 1.5rem;
}

li {
  list-style: none;
  display: block;

  font-size: 1rem;
  font-family: 'Ubuntu Mono', monospace;
}

footer a:link {
  color: #444444;
}

footer a:visited {
  color: #444444;
}
  

footer a:hover {
  color: #9898EC;
  text-decoration: underline;
}
